.results {
	/* background-color: green; */
	display: flex;
	flex-direction: column;
	height: 60rem;
	/* flex: 1; */
	/* width: 25%; */
	width: 50%;
}

.summary {
	/* background-color: tomato; */
	height: 75%;
	display: flex;
	flex-wrap: wrap;
}

.btn-container {
  /* background-color: tan; */
  margin-top: 2rem;
	height: 25%;
	display: flex;
	align-items: center;
	justify-content: center;
}

.summary-detail {
	/* background-color: skyblue; */
	/* border: 0.3px solid gray; */
	width: 50%;
	display: flex;
	align-items: center;
  justify-content: space-between;
  max-height: 55px;
  padding-left: 2rem;
    padding-right: 2rem;
}

.summary-detail-quantity {
	background-color: white;
	padding: 0.8rem;
	font-size: 2.5rem;
	border: 0.5px solid black;
	font-weight: 600;
}

.summary-detail-desc {
	font-size: 2.5rem;
}

@media only screen and (max-width: 750px) {
	.results {
		height: 20rem;
		width: 100%;
  }
  
  .summary-detail {
    margin-bottom: .1rem;  
    padding-left: 2rem;
    padding-right: 2rem;
    
  }

	.summary-detail-quantity {
    text-align: center;
    width: 4rem;
		padding: 0.8rem;
    font-size: 2rem;
    font-weight: 500;
	}

	.summary-detail-desc {
		font-size: 1.5rem;
  }

  
  .btn {
    background-color: rgb(1, 251, 34);
    padding: 1rem;
    width: auto;
    border: 1px solid black;
    border-radius: 50rem;
    font-size: 2rem;
    text-align: center;
    font-weight: 500;
    letter-spacing: -.1rem;

  }
  .btn-whatsapp {
    background-color: rgb(1, 251, 34);
    padding: 1rem;
    width: auto;
    border: 1px solid black;
    border-radius: 50rem;
    font-size: 2rem;
    text-align: center;
    font-weight: 500;
		letter-spacing: -.1rem;
		text-decoration: none;
		color: black;

	}
	
	.btn-whatsapp:visited {
		text-decoration: none
	}

  .dozen-wrapper {
    display: flex;
    flex-direction: column;
  }
}
