
.client-list {
	display: flex;
	/* flex-direction: column; */
	overflow-x: scroll;
	/* width: 40%; */
  color: blue;
	padding-left: 2rem;
	/* overflow-x: hidden; */
	height: 6rem;
	background-color: beige;
}

.client-name {
	height: 100%;
	border: 1px solid;
	padding: 2rem;
	user-select: none;
	/* height: 4rem; */
	/* background-color: aqua; */
	display: flex;
	align-items: center;
	font-size: 2rem;
  text-align: left;
	text-transform: uppercase;
	cursor: pointer;
}

.client-selected {
	text-decoration: underline;

}



.client-name:hover{
  cursor: pointer;
  text-decoration: underline;
}

@media only screen and (max-width: 750px) {
	
.client-list {
		padding-left: 2rem;
	}
}