.header {

  display: flex;
  height: 10rem;
  margin-bottom: 4rem;

  /* flex: 1 1 0; */
  /* height: 10vh; */
  /* flex: 1; */
  /* width: 25%; */
  /* height: 5rem; */
}

.logo {
  display: flex;
  /* background-color: rgb(170, 163, 163); */
  width: 50%;
  justify-content: space-evenly;
  

  
}

.username-input {
  display: flex;
  background-color: #0AFF89;
  width: 50%;
}


.logo-written{
  font-family: 'Roboto Thin', sans-serif;
  /* background-color: #f1f1f1; */
  font-size: 4rem;
  letter-spacing: -.2rem;
  /* padding-top: 8px; */
  display: flex;
  justify-content: center;
  align-items: center;

}

.logo-image{
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  
  /* flex: 0.3 1; */
  /* min-width: 90px; */

}

.icon {
  margin-bottom: .5rem;
}


.logo-jpg {
  height: 90%;
}
     
.username {
  display: flex;
  flex-direction: column;
  width: 80%;
  align-items: center;
  justify-content: center;
  /* padding-top: .6rem;
  padding-left: 2.5rem; */
  /* background-color: rebeccapurple; */
}
input {
  border: 1px solid #000000;
  padding: 8px;
  border-radius: 5px;
  /* max-width: 80%; */
  height: 5rem;
  text-transform: uppercase;
  
}

.buttons {
  display: flex;
  justify-content: center;
  align-content: center;
  width: 50%;
  padding: 2rem;
}
.btn-add-user {
  border-right: 1px solid rgb(49, 49, 49);
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  text-align: center;
}

.btn-add-user:active{
  background-color: #b3b3b375;
}

.btn-reset-app {
  text-align: center;
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  
}

.input-label {
  font-family: 'Roboto Light';
  /* background-color: #3498db; */
  font-size: 1.3rem;
  text-align: left;

}

.add-user {
  height: 3rem;
  width: 3rem;
}

@media only screen and (max-width: 750px) {

  .logo {
    display: none;
}
  .header {
    height: 10rem;
    margin-bottom: -4rem;
  }

  .logo, .username-input {
    width: 100%;
    height: 60%;
  }

  .buttons {
    padding: .7rem;
  }

  .logo-written{
    font-size: 3rem;

  
  }

  input {

    /* max-width: 80%; */
    height: 3rem;
    
  }

}