
.product-list {
	width: 100%;
	/* background-color: rgb(0, 109, 128); */
	overflow-y: scroll;
	padding: 3rem;
	height: 100%;
}

.product {
	display: flex;
	height: 40px;
	border-bottom: 2px solid black;
}

.product-name {
  text-transform: uppercase;
	/* padding: 1rem; */
	/* background-color: rgb(80, 80, 80); */
	color: black;
	font-size: 1.3rem;
	width: 75%;
	display: flex;
	align-items: center;
	
}

.product-select {
	width: 25%;
	border: 0px;
	border-bottom: 5px solid rgb(228, 16, 182);
	padding-left: 14px;
	font-size: 20px;
}



.product-count {
  /* background-color: #0AFF89; */
  width: 25%;
	border-bottom: 4px solid rgb(255, 10, 112);
	display: flex;
	justify-content: flex-end;
	align-items: center;
	font-size: 2.3rem;
	color: black;
  font-weight: 500;
  padding-right: 1.3rem
}