@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:400&subset=latin,latin-ext);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {

  display: flex;
  height: 10rem;
  margin-bottom: 4rem;

  /* flex: 1 1 0; */
  /* height: 10vh; */
  /* flex: 1; */
  /* width: 25%; */
  /* height: 5rem; */
}

.logo {
  display: flex;
  /* background-color: rgb(170, 163, 163); */
  width: 50%;
  justify-content: space-evenly;
  

  
}

.username-input {
  display: flex;
  background-color: #0AFF89;
  width: 50%;
}


.logo-written{
  font-family: 'Roboto Thin', sans-serif;
  /* background-color: #f1f1f1; */
  font-size: 4rem;
  letter-spacing: -.2rem;
  /* padding-top: 8px; */
  display: flex;
  justify-content: center;
  align-items: center;

}

.logo-image{
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  
  /* flex: 0.3 1; */
  /* min-width: 90px; */

}

.icon {
  margin-bottom: .5rem;
}


.logo-jpg {
  height: 90%;
}
     
.username {
  display: flex;
  flex-direction: column;
  width: 80%;
  align-items: center;
  justify-content: center;
  /* padding-top: .6rem;
  padding-left: 2.5rem; */
  /* background-color: rebeccapurple; */
}
input {
  border: 1px solid #000000;
  padding: 8px;
  border-radius: 5px;
  /* max-width: 80%; */
  height: 5rem;
  text-transform: uppercase;
  
}

.buttons {
  display: flex;
  justify-content: center;
  align-content: center;
  width: 50%;
  padding: 2rem;
}
.btn-add-user {
  border-right: 1px solid rgb(49, 49, 49);
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  text-align: center;
}

.btn-add-user:active{
  background-color: #b3b3b375;
}

.btn-reset-app {
  text-align: center;
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  
}

.input-label {
  font-family: 'Roboto Light';
  /* background-color: #3498db; */
  font-size: 1.3rem;
  text-align: left;

}

.add-user {
  height: 3rem;
  width: 3rem;
}

@media only screen and (max-width: 750px) {

  .logo {
    display: none;
}
  .header {
    height: 10rem;
    margin-bottom: -4rem;
  }

  .logo, .username-input {
    width: 100%;
    height: 60%;
  }

  .buttons {
    padding: .7rem;
  }

  .logo-written{
    font-size: 3rem;

  
  }

  input {

    /* max-width: 80%; */
    height: 3rem;
    
  }

}

.client-list {
	display: flex;
	/* flex-direction: column; */
	overflow-x: scroll;
	/* width: 40%; */
  color: blue;
	padding-left: 2rem;
	/* overflow-x: hidden; */
	height: 6rem;
	background-color: beige;
}

.client-name {
	height: 100%;
	border: 1px solid;
	padding: 2rem;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	/* height: 4rem; */
	/* background-color: aqua; */
	display: flex;
	align-items: center;
	font-size: 2rem;
  text-align: left;
	text-transform: uppercase;
	cursor: pointer;
}

.client-selected {
	text-decoration: underline;

}



.client-name:hover{
  cursor: pointer;
  text-decoration: underline;
}

@media only screen and (max-width: 750px) {
	
.client-list {
		padding-left: 2rem;
	}
}

.product-list {
	width: 100%;
	/* background-color: rgb(0, 109, 128); */
	overflow-y: scroll;
	padding: 3rem;
	height: 100%;
}

.product {
	display: flex;
	height: 40px;
	border-bottom: 2px solid black;
}

.product-name {
  text-transform: uppercase;
	/* padding: 1rem; */
	/* background-color: rgb(80, 80, 80); */
	color: black;
	font-size: 1.3rem;
	width: 75%;
	display: flex;
	align-items: center;
	
}

.product-select {
	width: 25%;
	border: 0px;
	border-bottom: 5px solid rgb(228, 16, 182);
	padding-left: 14px;
	font-size: 20px;
}



.product-count {
  /* background-color: #0AFF89; */
  width: 25%;
	border-bottom: 4px solid rgb(255, 10, 112);
	display: flex;
	justify-content: flex-end;
	align-items: center;
	font-size: 2.3rem;
	color: black;
  font-weight: 500;
  padding-right: 1.3rem
}
.calculations {
	/* background-color: rgb(221, 221, 221); */
	/* flex: 1; */
	/* width: 25%; */
	height: 60rem;
	width: 50%;
	display: flex;
  color: blue;
}

@media only screen and (max-width: 750px) {
	.calculations {
		height: 30rem;

		width: 100%;
		display: flex;
		flex-direction: column;
	}
}



.results {
	/* background-color: green; */
	display: flex;
	flex-direction: column;
	height: 60rem;
	/* flex: 1; */
	/* width: 25%; */
	width: 50%;
}

.summary {
	/* background-color: tomato; */
	height: 75%;
	display: flex;
	flex-wrap: wrap;
}

.btn-container {
  /* background-color: tan; */
  margin-top: 2rem;
	height: 25%;
	display: flex;
	align-items: center;
	justify-content: center;
}

.summary-detail {
	/* background-color: skyblue; */
	/* border: 0.3px solid gray; */
	width: 50%;
	display: flex;
	align-items: center;
  justify-content: space-between;
  max-height: 55px;
  padding-left: 2rem;
    padding-right: 2rem;
}

.summary-detail-quantity {
	background-color: white;
	padding: 0.8rem;
	font-size: 2.5rem;
	border: 0.5px solid black;
	font-weight: 600;
}

.summary-detail-desc {
	font-size: 2.5rem;
}

@media only screen and (max-width: 750px) {
	.results {
		height: 20rem;
		width: 100%;
  }
  
  .summary-detail {
    margin-bottom: .1rem;  
    padding-left: 2rem;
    padding-right: 2rem;
    
  }

	.summary-detail-quantity {
    text-align: center;
    width: 4rem;
		padding: 0.8rem;
    font-size: 2rem;
    font-weight: 500;
	}

	.summary-detail-desc {
		font-size: 1.5rem;
  }

  
  .btn {
    background-color: rgb(1, 251, 34);
    padding: 1rem;
    width: auto;
    border: 1px solid black;
    border-radius: 50rem;
    font-size: 2rem;
    text-align: center;
    font-weight: 500;
    letter-spacing: -.1rem;

  }
  .btn-whatsapp {
    background-color: rgb(1, 251, 34);
    padding: 1rem;
    width: auto;
    border: 1px solid black;
    border-radius: 50rem;
    font-size: 2rem;
    text-align: center;
    font-weight: 500;
		letter-spacing: -.1rem;
		text-decoration: none;
		color: black;

	}
	
	.btn-whatsapp:visited {
		text-decoration: none
	}

  .dozen-wrapper {
    display: flex;
    flex-direction: column;
  }
}

.footer{
  background-color: rgb(202, 202, 202);
  /* flex: 1; */
  /* width: 25%; */
  height: 5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3rem;
  font-weight: lighter;


}

@media only screen and (max-width: 750px) {
  .footer {
    height: 5rem;
  }
}

/*USING CSS VARIABLES*/
::root{
  --color-primary: #adf32d;
}




.App {
  /* text-align: center; */
  /* background-color: var(--color-primary); */
  /* max-height: 10vh; */

  /* background-color: tomato; */
  border: 1px solid lightblue;
  /*let's treat this as container*/
  display: flex; 
  flex-direction: column;
  height: 85vh; 
  /* flex-wrap: wrap; */
  max-width: 70vw;
  margin: 0 auto;
  margin-top: 50px;
  /* background-color: #DFDFDF; */
  box-shadow: -2px 2px 4px rgba(0, 0, 0, 0.25);
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.middle {
  display: flex;
  height: 78%;
  flex-direction: row-reverse;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

* {
  box-sizing: border-box;
}

